  font-weight: bold;
}

html {
  --lh: 1.4rem;
  line-height: 1.4rem;
  line-height: var(--lh);
}
.overflow {
  --max-lines: 3;
  position: relative;
  max-height: calc(1.4rem * var(--max-lines));
  max-height: calc(var(--lh) * var(--max-lines));
  overflow: hidden;
  padding-right: 1rem; /* space for ellipsis */
}
.overflow::before {
  position: absolute;
  /* content: "..."; */
/*   inset-block-end: 0;
  inset-inline-end: 0; */
  bottom: 0;
  right: 0;
}
.overflow::after {
  content: "";
  position: absolute;
/*   inset-inline-end: 0; */
  right: 0;
  width: 1rem;
  height: 1rem;
  background: white;
}

